.main {
    height: 100vh;

    background-color: #000000;
    display: flex;
    justify-content: center;
    padding: 100px 2px;


}

.container {
    width: 99%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;

}

.greeting {
    display: flex;
    flex-direction: column;

}

.greeting span:nth-child(1) {
    font-size: 70px;
    font-weight: 500;
    font-stretch: normal;

}

.greeting span:nth-child(2) {
    font-size: 59px;
    font-weight: 500;
    font-stretch: normal;
}
.greeting span:nth-child(2) span {
    font-size: 49px;
    font-weight: 500;
    font-stretch: normal;
    position: relative;
    z-index: 1;
}
.greeting span:nth-child(2) span:before {
   content: '';
    width: 170px;
    height: 18px;

    bottom: 1px;
    position: absolute;
    background-color: #581ffa;
    z-index: -1;
}

@media screen and (max-width: 320px) {
    .greeting span:nth-child(2) span:before {
       background-color: #2ef2d9;
        height: 2vh;
        width: 100px;
        border-radius: 15px;
    }
}

.greeting h1{
    font-family: 'Play', sans-serif;
    color: #f3f8ff;
    font-weight: 700;
    font-size: 27px;
}

.photo {
    margin-top: 22px;
    position: relative;
    z-index: 1;
}

.image {

    height: 350px;
    width: 300px;
    background-color: gray;

}
.photo .image:before {
    content: '';
    position: absolute;
    height: 370px;
    top: -15px;
    left: 8px;
    width: 300px;
    z-index: -1 ;
    border: 5px solid #581ffa;

}