.skillsBlock {
    height: 100%;
    background-color: #020200;
    display: flex;
    justify-content: center;
}

.container {
    width: 80%;

    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 5px;
}


.headerTitle {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: 'Poiret One', cursive;
    font-weight: 700;
    font-size: 33px;
}

.line {
    height: 1px;
    width: 55px;
    margin: 30px 0px;
    background-color: #581ffa;
}


.title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.skills {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    width: 100%;
}

@media screen and (max-width: 1164px) {
    .skills {
        justify-content: center;
        align-items: center;
    }
}